.outside {
    position: relative;
    /* float: right; */
    background-color: #fff;
    width: 40%;
    overflow: hidden;
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,.2);
    padding: 1em;
    z-index: 0;
    height: 350px;
}
.border-box {
    text-align: end;
    top: 20px;
    left: 15px;
    width: 200px;
    height: 230px;
    position: absolute;
    line-height: 215px;
    font-family: monospace;
    background-color: #fcfbe5;
    border: solid 5px #e3e0a1;
    /* z-index: auto; */
    z-index: 0;background: 0 0;position: relative;
}
.position1 {
    top: 30px;
    left: 0;
    z-index: 6;
}
.position2 {
    top: 60px;
    left: 30px;
    z-index: 4;
}
.position3 {
    top: 90px;
    left: 60px;
    z-index: -1;
}
.position4 {
    top: 150px;
    left: 0;
    z-index: auto;
}
.position5 {
    top: 180px;
    left: 30px;
    z-index: 2;
}
.position6 {
    top: 220px;
    left: 60px;
    z-index: 0;
}
.red {
    background-color: #fce5e7;
    border: solid 5px #e3a1a7;
}
.blue {
    background-color: #e5e8fc;
    border: solid 5px #112382;
    /* line-height: 55px; */
}
.block {
    width: 90px;
    height: 50px;
    position: absolute;
    font-family: monospace;
}
/* 垂直首部 */
.flex-start{
    display: flex;
    align-items: flex-start;
}
/* 垂直居中 */
.flex-center{
    display: flex;
    align-items: center;
}
/* 垂直末尾 */
.flex-end{
    display: flex;
    align-items: flex-end;
}