@keyframes resize {
    0% {
        padding: 0;
        background-color: rgba(60, 60, 60, 0);
        height: 0px;
        width: 0px;
        border-radius: 10px;
        font-size: 0px;
        color: #FFF;
        top: 50px;
    }
    15% {
        height: 1px;
        width: 10px;
        border-radius: 10px;
        background-color: rgba(60, 60, 60, 4);
    }
    25% {
        height: 1px;
        width: 100%;
        top: 50px;
    }
    40% {
        height: 100%;
        top: 0;
        background-color: rgba(60, 60, 60, 1);
    }
    60% {
        background-color: rgba(0, 136, 204, 1);
        font-size: 0px;
    }
    70% {
        font-size: 40px;
    }
    100% {
        background-color: rgba(12, 170, 244, 1);
    }
}

.dialog-box-container {
    height: 100px;
    /* width: 100%; */
    margin: 0 auto;
}

.dialog-box-container .dialog-box {
    height: 100%;
    width: 100%;
    border-radius: 10px;
    background-color: rgba(0, 136, 204, 1);
    font-size: 40px;
    color: #FFF;
    text-align: center;
    line-height: 100px;
    margin: 0 auto;
    top: 0;
    position: relative;
    -webkit-animation-name: resize;
    -webkit-animation-duration: 5s;
    /*-webkit-animation-iteration-count: infinite;*/
    -webkit-animation-direction: alternate;

    -moz-animation-name: resize;
    -moz-animation-duration: 5s;
    /*-moz-animation-iteration-count: infinite;*/
    -moz-animation-direction: alternate;

    -o-animation-name: resize;
    -o-animation-duration: 5s;
    /*-o-animation-iteration-count: infinite;*/
    -o-animation-direction: alternate;

    animation-name: resize;
    animation-duration: 5s;
    /*animation-iteration-count: infinite;*/
    animation-direction: alternate;
}

.animation-once {
    animation-iteration-count: 1;
}

.animation-infinite {
    animation-iteration-count: infinite;
}

.dynamic-border::before,
.dynamic-border::after {
    position: absolute;
    left: 0;
    content: "";
    display: block;
    width: 1px;
    height: 0px;
    background: #333;
    z-index: 1;
}

.dynamic-border::before {
    top: 0;
}

.dynamic-border::after {
    bottom: 0;
}

.dynamic-border .border-inner-contents {
    position: relative;
    /*display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;*/
    -o-box-align: center;
    align-items: center;
    /*元素在主轴上的对齐方式*/
    justify-content: center;
    width: 100%;
    height: 100%;
    min-height: 50px;
}

.dynamic-border .border-inner-contents::before,
.dynamic-border .border-inner-contents::after {
    position: absolute;
    right: 0;
    content: "";
    display: block;
    width: 0px;
    height: 1px;
    background: #3333;
    z-index: 1;
}

.dynamic-border .border-inner-contents::before {
    top: 0;
}

.dynamic-border .border-inner-contents::after {
    bottom: 0;
}


.dynamic-border {
    position: relative;
    display: flex;

    /*旧的flex-box草案属性*/
    /*-webkit-box-align: center;
    -moz-box-align: center;
    -o-box-align: center;*/

    /*元素在侧轴上的对齐方式*/
    align-items: center;
    /*元素在主轴上的对齐方式*/
    justify-content: center;

    /*文本装饰: 下划线, 上划线, 直线, 组合线 等*/
    text-decoration: none;
    color: #c4d700;
    background-color: rgba(196, 215, 0, 0.15);
    /*width: 10%;*/
}
/*左侧边缘线*/
.dynamic-border::before,
.dynamic-border::after {
    background: #c4d700;
}

.dynamic-border .border-inner-contents::before,
.dynamic-border .border-inner-contents::after {
    background: #c4d700;
}

/*响应式自适应*/
@media screen and (min-width: 751px) {
    /*只在751px以上的设备中才使用动画*/
    .dynamic-border:hover .border-inner-contents::before,
    .dynamic-border:hover .border-inner-contents::after {
        width: 0;
        animation: hoverLine-2 0.5s ease forwards 0.15s;
    }
    /*
    tips:
    0.同一选择器无法应用多个动画
    1.time controler
    */
    .dynamic-border:hover::before,
    .dynamic-border:hover::after {
        animation: hoverLine-3 0.15s ease forwards 0.7s;
    }

    .dynamic-border {
        display: block;
    }
}

@keyframes hoverLine-2 {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}

@keyframes hoverLine-3 {
    0% {
        height: 0;
    }
    100% {
        height: 50%;
    }
}


@keyframes rocket-keyframe-1 {
    0% {
        top: 0%;
        left: 0%;
    }
    100% {
        top: 60%;
        left: 0%;
        background: red;
    }
}

