  font-weight: 700
}

/* 密码输入框 */
.strength-container .strength-form input[type=password], .strength-container .strength-form input[type=text] {
  background: 0 0;
  border: 2px solid #09f;
  color: #777;
  font-family: lato, sans-serif;
  font-size: 14px;
  padding: 9px 5px;
  height: 21px;
  text-indent: 6px;
  -webkit-appearance: none;
  border-radius: 6px;
  box-shadow: none;
  transition: border .25s linear, color .25s linear;
  -webkit-backface-visibility: hidden;
  width: 100%
}

.strength-container .strength-form input[type=password]:focus, .strength-container .strength-form input[type=text]:focus {
  outline: 0;
  border: 2px solid rgb(148, 146, 146)
}

/* 仪表区 */
.strength-container .strength_meter {
  position: absolute;
  left: 0;
  top: 40%;
  width: 100%;
  height: 30%;
  z-index: -1;
  border-radius: 5px
}

/* 显示/隐藏密码按钮 */
.strength-container .button_strength {
  text-decoration: none;
  color: rgb(119, 117, 117);
  font-size: 13px
}

/* 强度提示填充 */
.strength-container .strength_meter div {
  width: 0%;
  height: 100%;
  text-align: right;
  color: #000;
  line-height: 43px;
  -webkit-transition: all .3s ease-in-out;
  padding-right: 12px;
  border-radius: 5px
}

/* 强度提示文字 */
.strength-container .strength_meter div p {
  position: absolute;
  top: -110%;
  right: 1%;
  color: rgb(187, 1, 1);
  font-size: 80%
}

/* 强度提示填充登记 */

.strength-container .veryweak {
  background-color: #ffa0a0;
  border-color: #f04040 !important;
  width: 25% !important
}

.strength-container .weak {
  background-color: #ffb78c;
  border-color: #ff853c !important;
  width: 50% !important
}

.strength-container .medium {
  background-color: #ffec8b;
  border-color: #fc0 !important;
  width: 75% !important
}

.strength-container .strong {
  background-color: #c3ff88;
