}

/*图像的左侧和右侧均不允许出现浮动元*/
.input-body .clear {
  clear: both;
}

.input-body {
  background-size: cover;
  /* background: RGB(58, 77, 84) center; */
  background-size: 100% 100%;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.input-body .main {
  margin: 7em auto 0;
  width: 44%;
}

/* 输入框 */
.input-body input[type="text"]:hover {
  color: #DCDCDC;
  border-left: 6px solid #40A46F;
}

.input-body input[type="text"] {
  padding: 14px;
  width: 100%;
  font-size: 1em;
  margin: 18px 0px;
  border: none;
  color: #666666;
  cursor: pointer;
  background: none;
  float: left;
  outline: none;
  font-weight: 700;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #ffffff;
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  border-left: 6px solid #fff;
  border-bottom: none;
  border-right: none;
  border-top: none;
}

.input-body .submit {
  padding: 5px 4px;
  text-align: center;
}

/* 提交按钮 */
.input-body input[type=submit] {
  padding: 17px 30px;
  color: #fff;
  float: right;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #40A46F;
  border: 1px solid #40A46F;
  cursor: pointer;
  font-size: 18px;
  transition: all 0.5s ease-out;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -ms-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  outline: none;
  width: 100%;
}

.input-body .submit input[type=submit]:hover {
  background: #07793D;
  border: 1px solid #07793D;
}

