.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
}

.App-header {
  background-color: #e3f5e8;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  /* 文字 */
  color: #282c34;
}

.App-toobar {
  background-color: rgba(155, 177, 138, 1);
  /* 文字 */
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.single-line-text-overflow-omitted {
  /* 文字长度超出限定宽度，则隐藏超出的内容 */
  text-overflow: ellipsis;
  /* 设置文字在一行显示，不能换行 */
  white-space: nowrap;
  /* 规定当文本溢出时，显示省略符号来代表被修剪的文本 */
  overflow: 'hidden'
}