.calc {
  background: rgba(170, 224, 222, 0.05);
  box-shadow: 0 1px 1px rgba(100, 100, 100, 0.5);
}

.bits {
  overflow: hidden;
}

.title{
  color: #1a0101;
}
.little-title{
  color: black;
}

.description{
  color: rgb(0, 255, 13);
}

.bits .bit {
  width: 20%;
  float: left;
  text-align: center;
  padding: 2px;
}

.modes table{
  /* 表格居中 */
  margin:0 auto;
  /* 根据宽度及表格宽度计算 不灵 */
  /* margin:0 50%; */
}

.modes table th,td{
  border-color: rgb(6, 201, 250);
}

.modes.read,
.modes.write,
.modes.execute {
  display: block;
  margin: 10px auto;
}


.results {
  border-top: 1px solid #666666;
  overflow: hidden;
}
.results .result {
  width: 25%;
  float: left;
  margin: 10px 0;
  text-align: center;
}